import React from 'react';
import PropTypes from 'prop-types';

import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import { selectIsCurrentThemesDark } from '../../redux/themes/themes.selectors';

import { GlobalStyles, LightThemesStyles, DarkThemesStyles } from './global.styles';
import Header from '../header/header.component';
import Footer from '../footer/footer.component';

const Layout = ({ children, darkThemes }) => {
  return (
    <>
      <GlobalStyles />
      {
        darkThemes ? <DarkThemesStyles /> : <LightThemesStyles />
      }
      <Header />
        <>{children}</>
      <Footer />
    </>
  );
};

const mapStateToProps = createStructuredSelector({
  darkThemes: selectIsCurrentThemesDark,
});

Layout.propTypes = {
  children: PropTypes.node.isRequired,
};

export default connect(mapStateToProps)(Layout);
